import { GET_USERS } from "./types";
import { APIManager } from "../helpers";
const { callMethod } = APIManager;

export function getUsersTop() {
  return (dispatch) => {
    callMethod("getUsersTop", {
      page: 0,
    }).then(({ status, message }) => {
      if (status === "success") {
        console.log("actions message", message);

        dispatch({ type: GET_USERS, payload: message });
      }
    });
  };
}
